<template>
  <div>
    <RightTitle title="空间设置"></RightTitle>

    <!-- 表单 -->
    <div class="uar_form">
      <el-form ref="form" :model="form" label-width="100px" size="medium">
        <el-form-item label="空间名称">
          <el-input v-model="form.spaceName" clearable placeholder="请输入空间名称" style="width:400px;"></el-input>
        </el-form-item>
        <el-form-item label="空间公告">
          <el-input
            type="textarea"
            v-model="form.notice"
            clearable
            placeholder="请输入空间公告"
            :rows="5"
            maxlength="50"
            show-word-limit
            resize="none"
          ></el-input>
        </el-form-item>
        <el-form-item label="空间模板">
          <div class="clear">
            <div class="avatar_list" @click="selectTemplate('default')">
              <el-tooltip content="默认模板">
                <el-badge :value="templateValue('default')" class="item">
                  <el-avatar
                    shape="square"
                    :size="100"
                    fit="cover"
                    src="http://www.aizhanzhe.com/Public/Media/Space/mb_default.jpg"
                  ></el-avatar>
                </el-badge>
              </el-tooltip>
            </div>
            <div class="avatar_list" @click="selectTemplate('default1')">
              <el-tooltip content="默认模板">
                <el-badge :value="templateValue('default1')" class="item">
                  <el-avatar
                    shape="square"
                    :size="100"
                    fit="cover"
                    src="http://www.aizhanzhe.com/Public/Media/Space/mb_default.jpg"
                  ></el-avatar>
                </el-badge>
              </el-tooltip>
            </div>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm">立即提交</el-button>
          <el-button @click="resetForm('form')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import RightTitle from "../../../components/RightTitle";
export default {
  name: "person_space",
  components: {
    RightTitle
  },
  data() {
    return {
      form: {
        spaceName: "",
        notice: "",
        spaceTemplate: "default1"
      }
    };
  },
  methods: {
    //   选择模板
    selectTemplate(item) {
      this.form.spaceTemplate = item;
    },
    templateValue(value) {
      return this.form.spaceTemplate == value ? "√" : "";
    },
    //   提交
    submitForm() {
      console.log(this.form);
    },
    // 重置
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style scoped>
.avatar_list {
  float: left;
  margin-right: 15px;
}
</style>